        p4: '–ö—Ä–µ–∫ ‚Äî —Å–ª–µ–Ω–≥–æ–≤–∞ –Ω–∞–∑–≤–∞ –æ–¥–Ω—ñ—î—ó –∑ —Ñ–æ—Ä–º –∫–æ–∫–∞—ó–Ω—É. –û—Å–æ–±–ª–∏–≤—ñ—Å—Ç—å –∫—Ä–µ–∫—É –ø–æ–ª—è–≥–∞—î –≤ —Ç–æ–º—É, —â–æ –π–æ–≥–æ –º–æ–∂–Ω–∞ –∫—É—Ä–∏—Ç–∏. –ü–∞—Ä–∏ –∫–æ–∫–∞—ó–Ω—É –ø–æ—Ç—Ä–∞–ø–ª—è—é—Ç—å –≤ –æ—Ä–≥–∞–Ω—ñ–∑–º, —á–µ—Ä–µ–∑ 8-10 —Å–µ–∫—É–Ω–¥ –≤–∏–∫–ª–∏–∫–∞—é—á–∏ –Ω–∞—Ä–∫–æ—Ç–∏—á–Ω–µ —Å–ø\'—è–Ω—ñ–Ω–Ω—è. –ö—Ä–∏—Å—Ç–∞–ª–∏ –∫–æ–∫–∞—ó–Ω—É –≥–æ—Ä—è—Ç—å –∑ —Ö–∞—Ä–∞–∫—Ç–µ—Ä–Ω–∏–º –∑–≤—É–∫–æ–º, –ø–æ—Ç—Ä—ñ—Å–∫—É–≤–∞–Ω–Ω—è–º (–∑–≤—ñ–¥—Å–∏ –Ω–∞–∑–≤–∞ ‚Äî –∑–≤—É–∫–æ—ñ–º—ñ—Ç–∞—Ü—ñ—è ¬´–∫—Ä–µ–∫¬ª).\n–®–≤–∏–¥–∫—ñ—Å—Ç—å –¥—ñ—ó –∫—Ä–µ–∫—É, –π–æ–≥–æ –¥–µ—à–µ–≤–∏–∑–Ω–∞ (1 –≥ –∫—Ä–µ–∫—É –ø—Ä–∏–±–ª–∏–∑–Ω–æ –≤ 2-3 —Ä–∞–∑–∏ –¥–µ—à–µ–≤—à–µ –∫–æ–∫–∞—ó–Ω—É —É –≤–∏–≥–ª—è–¥—ñ –≥—ñ–¥—Ä–æ—Ö–ª–æ—Ä–∏–¥—É) —ñ –ø—Ä–∏–¥–∞—Ç–Ω—ñ—Å—Ç—å –¥–ª—è –∫—É—Ä—ñ–Ω–Ω—è –∑–∞–±–µ–∑–ø–µ—á–∏–ª–∏ –π–æ–º—É —á–∏–º–∞–ª—É –ø–æ–ø—É–ª—è—Ä–Ω—ñ—Å—Ç—å. –ö—Ä–µ–∫ –ª–µ–≥–∫–æ –º–∞—Å–∫—É—î—Ç—å—Å—è –ø—ñ–¥ –∑–≤–∏—á–∞–π–Ω—ñ —Å–∏–≥–∞—Ä–µ—Ç–∏.\n–°–µ—Ä–µ–¥ –Ω–∞—Ä–∫–æ–º–∞–Ω—ñ–≤ —ñ—Å–Ω—É—î –¥—É–º–∫–∞ –ø—Ä–æ –≤—ñ–¥–Ω–æ—Å–Ω—É –±–µ–∑–ø–µ—á–Ω—ñ—Å—Ç—å –∫—Ä–µ–∫—É –≤ –ø–æ—Ä—ñ–≤–Ω—è–Ω–Ω—ñ –∑ –∫–æ–∫–∞—ó–Ω–æ–º. –¶–µ –Ω–∞—Å–ª—ñ–¥–æ–∫ –µ–ª–µ–º–µ–Ω—Ç–∞—Ä–Ω–æ—ó –±–µ–∑–≥—Ä–∞–º–æ—Ç–Ω–æ—Å—Ç—ñ, –æ—Å–∫—ñ–ª—å–∫–∏ –∑–∞ —Å–≤–æ—ó–º —Ö—ñ–º—ñ—á–Ω–∏–º —Å–∫–ª–∞–¥–æ–º –∫—Ä–µ–∫ –∞–Ω–∞–ª–æ–≥—ñ—á–Ω–∏–π –∫–æ–∫–∞—ó–Ω—É, –∫—Ä—ñ–º —Ç–æ–≥–æ, –º–∞—î –±–µ–∑–ª—ñ—á –æ—Ç—Ä—É–π–Ω–∏—Ö –¥–æ–º—ñ—à–æ–∫. –£ —Ç–∏—Ö, —Ö—Ç–æ –∫—É—Ä–∏—Ç—å –∫—Ä–µ–∫, —Ä–æ–∑–≤–∏–≤–∞—î—Ç—å—Å—è —Å–ø–µ—Ü–∏—Ñ—ñ—á–Ω–∏–π –∫–æ–º–ø–ª–µ–∫—Å –∑–∞—Ö–≤–æ—Ä—é–≤–∞–Ω—å –ª–µ–≥–µ–Ω—ñ–≤, –≤—ñ–¥–æ–º–∏–π —è–∫ "–∫—Ä–µ–∫–æ–≤—ñ –ª–µ–≥–µ–Ω—ñ": —Ü–µ —Ö—Ä–æ–Ω—ñ—á–Ω–∏–π –±—Ä–æ–Ω—Ö—ñ—Ç, —Å–∫–ª–µ—Ä–æ–∑ –ª–µ–≥–µ–Ω–µ–≤–æ—ó —Ç–∫–∞–Ω–∏–Ω–∏ —ñ –ø–æ—à–∫–æ–¥–∂–µ–Ω–Ω—è –Ω–∞–π–¥—Ä—ñ–±–Ω—ñ—à–∏—Ö –ª–µ–≥–µ–Ω–µ–≤–∏—Ö —Å—É–¥–∏–Ω.',
